body{
  background-color:#1C232A ;
  color:#fff;
  font-family: 'Red Hat Display', sans-serif;
}
.Home{
display: flex;  
/* border: green 5px solid;  */
margin: 0 10%;
}
.t_box{
    display: flex;
    height: 90vh;
}
.left_box{
    /* border: red 5px solid;    */
    width: 60%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fix_text{
    /* border: blue 5px solid; */
    width: 70%;  
    margin-left: 90px;
    display: flex;
    justify-content: center; 
}
.left_box button{
    margin-top: 20px;
    background-color: #1BA3C5;
    border: none;
    width: 100px;
    cursor: pointer;
    border-radius: 10px;
    height: 30px;
}
button:hover{
    background-color: #34525a;
}
.right_box{
    /* border: red 5px solid;    */
    width: 50%;
    align-items: center;
    }
.icon_box{
    padding:0 10px;
    font-size: 22px;
}
.icn{
    padding-top: 100px;
    margin: 10px;
}
.icon_box a{
    color: #ffffff;
}
.fix_text{
    font-size: 30px;
}
.title_3{
    color: #1BA3C5;
    font-size: 28px;
}
.text{
    font-size: 20px;
    font-family:monospace;
    color: #FF3A16;
    font-weight: 700;
}
.mailBox{
    font-size: 30px;
}
.b_ton button{
    font-size: 15px;
}
@media screen and (max-width:900px) {
    .Home{
        flex-direction: column;
        margin: 0;
    }
    .left_box{
        width: 100%;
        height:60vh;
        align-items: center;
    }
    .right_box{
        width:100%;
        align-items: unset;
        height:30vh;
        justify-content: center;
    }
    .fix_text{
        width: 50%;
        margin: 0;
    }
    .icn{
        padding-top: 30px;
    }
        /* .t_box{
            height:0vh;
        } */
}

.popup{
    position:fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7px );
    -webkit-backdrop-filter: blur( 17px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    display: flex;
    justify-content: center;
    align-items: center;
}
.pop-inner{
    position: relative;
    padding: 22px;
    width: 80%;
    max-width:640px;
    backdrop-filter: blur(8px) saturate(127%);
    -webkit-backdrop-filter: blur(8px) saturate(127%);
    background-color: rgba(85, 154, 172, 0.91);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.125);
}
.pop-inner button{
    /* width: 100px; */
    	border: none;
        background-color:#3e7581;
        border-radius: 5px;
        padding: 2px;
        width: 40px;
        height: 30px;
        font-size: 20px;
        font-weight: bold;
}
.pop-inner h3{
    color:#FF3A16;
    word-spacing: 2px;
}
